              <tr>
                <td style="width: 20%">
                  <SkeletonLoading style="width: 100%;" />
                </td>
                <td style="width: 60%">
                  <SkeletonLoading style="width: 100%;" />
                </td>
                <td style="width: 20%">
                  <SkeletonLoading style="width: 100%;" />
                </td>
              </tr>
            {/each}
          </table>
        {:then loadedBalances}
          {#if loadedBalances.length === 0}
            <p>You don't have any tokens!</p>
          {:else}
            <table>
              <tr style="width: 100%">
                <th>Name</th>
                <th>ID</th>
                <th>Amount</th>
              </tr>
