                </td>
                <td style="width: 20%">
                  <SkeletonLoading style={'width: 100%'} />
                </td>
              </tr>
            {/each}
          {:then loadedTxs}
            {#each loadedTxs as tx}
              <tr>
                <td style="width: 70%">
                  <a
                    href="https://viewblock.io/arweave/tx/{tx.id}"
                    class="transaction">
                    <span class="direction">{tx.type}</span>
                    {tx.id}
                  </a>
                  <span class="status {tx.status}" />
                </td>
                <td style="width: 20%">{tx.amount} AR</td>
              </tr>
            {/each}
          {/await}
        </table>
      {:else if activeMenu === 'supported'}
        <table in:fade={{ duration: 400 }}>
          {#await supportedTokens}
            {#each Array(5) as _}
              <tr>
                <td style="width: 30%">
                  <SkeletonLoading style={'width: 100%'} />
                </td>
                <td style="width: 20%">
                  <SkeletonLoading style={'width: 100%'} />
                </td>
                <td style="width: 50%">
                  <SkeletonLoading style={'width: 100%'} />
                </td>
              </tr>
            {/each}
          {:then loadedTokens}
            <tr>
              <th>Token</th>
              <th>Ticker</th>
              <th>ID</th>
            </tr>
            {#if loadedTokens.length === 0}
              <p
                style="position: absolute; left: 50%; transform: translateX(-50%);">
