  padding-bottom: 2.5em;
}
.balance p, .balance div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--secondary-text-color);
  text-transform: uppercase;
  font-size: 0.9em;
  margin: 0;
  font-weight: 600;
}
.balance p .select-container, .balance div .select-container {
  display: inline-block;
}
.balance p.wallet, .balance div.wallet {
  text-transform: none;
