  opacity: 0.7;
}
@media screen and (max-width: 720px) {
  .balance p.wallet, .balance div.wallet {
    overflow-wrap: anywhere;
    max-width: 90vw;
  }
}
.balance h1.total-balance {
  font-size: 2.3em;
  color: var(--primary-text-color);
