<script>
  import { notification } from "../stores/notificationStore";
  import { NotificationType } from "../utils/types";
  import { cubicIn } from "svelte/easing";
  import { fly, scale, fade } from "svelte/transition";
  import errorIcon from "../assets/notification/error.svg";
  import warningIcon from "../assets/notification/warning.svg";
  import successIcon from "../assets/notification/success.svg";
  import infoIcon from "../assets/notification/info.svg";
  import closeIcon from "../assets/notification/close.svg";

  let timeout = null,
    hover = false;

  $: notificationClass = $notification === null ? "" : $notification.type;

  function hide() {
    notification.remove();
    timeout = null;
    hover = false;
  }

  $: {
    if ($notification !== null && timeout === null)
      timeout = setTimeout(hide, $notification.timeout);
  }
</script>

{#if $notification !== null}
  <div
    class="notification {notificationClass}"
    in:fly={{ x: 1000, opacity: 0, easing: cubicIn }}
    out:scale={{ duration: 200 }}
    on:click={hide}
    on:mouseover={() => (hover = true)}
    on:mouseleave={() => (hover = false)}>
    {#if hover}
      <img
        class="notification-icon"
        src={closeIcon}
        alt="close-notification"
        in:fade={{ duration: 300 }} />
    {:else}
      <img
        class="notification-icon"
        src={notificationClass === 'error' ? errorIcon : notificationClass === 'warning' ? warningIcon : notificationClass === 'success' ? successIcon : infoIcon}
        alt={notificationClass}
        in:fade={{ duration: 300 }} />
    {/if}
    <div class="notification-content">
      <h1 class={notificationClass}>[{$notification.title}]</h1>
      <p>{$notification.description}</p>
    </div>
  </div>
{/if}

<style lang="sass">.notification {
  position: fixed;
  bottom: 1.4em;
  right: 1em;
  padding: 0.7em 1em;
  background-color: rgba(18, 18, 18, 0.975);
  border-radius: 5px;
  box-shadow: -2px 6px 17px 8px rgba(0, 0, 0, 0.18);
  z-index: 100000;
  cursor: pointer;
  overflow: hidden;
  max-width: 35vw;
  display: flex;
  align-items: center;
  transition: all 0.3s;
}
@media screen and (max-width: 720px) {
  .notification {
    bottom: unset;
    top: 1.2em;
    left: 1.2em;
    right: 1.2em;
    max-width: calc(100vw - 2.4em);
  }
}
.notification img.notification-icon {
  margin-right: 0.8em;
  height: 1.2em;
}
.notification:hover {
  box-shadow: -2px 6px 17px 8px rgba(0, 0, 0, 0.26);
}
.notification .notification-content h1 {
  margin: 0;
  margin-bottom: 0.3em;
  font-size: 1.05em;
  font-weight: 400;
}
.notification .notification-content h1.error {
  color: #FF375D;
}
.notification .notification-content h1.warning {
  color: #FFD336;
}
.notification .notification-content h1.success {
  color: #00D46E;
}
.notification .notification-content h1.log {
  color: #F2F2F2;
}
.notification .notification-content p {
  color: #fff;
  font-size: 1.05em;
  margin: 0;
  font-weight: 400;
}</style>
