          <th>Quantity</th>
          <th>Rate</th>
          <th>Filled</th>
        </tr>
        {#await orderBook}
          {#each Array(5) as _}
            <tr>
              <td style="width: 10%">
                <SkeletonLoading style="width: 100%;" />
              </td>
              <td style="width: 30%">
                <SkeletonLoading style="width: 100%;" />
              </td>
              <td style="width: 30%">
                <SkeletonLoading style="width: 100%;" />
              </td>
              <td style="width: 30%">
                <SkeletonLoading style="width: 100%;" />
              </td>
            </tr>
          {/each}
        {:then loadedOrders}
